<template>
<link rel="stylesheet" href="https://cdnjs.cloudflare.com/ajax/libs/font-awesome/5.15.3/css/all.min.css" integrity="sha512-************" crossorigin="anonymous" />

  <header class="header">

    <div class="container">

      <div id="navbar" class="navbar">

        <a href="/" class="logo-link">
          <h2 class="logo">BYM</h2>
        </a>

        <a href="/" class="logo-link">
          <img :src="require('@/assets/img/apple-bg.png')" class="knifelogo" />
        </a>

        <div class="body-header__search">
          <div class="search-wrapper">

            <i class="fas fa-search search-icon"></i>

            <input
              name="search"
              v-model="search"
              type="text"
              id="search-input-id"
              class="search_bar"
              aria-label="Поиск"
              autocomplete="off"
              placeholder="Найти блюдо"
              @input="logInput"
            />
          </div>
        </div>

        <div>
          <button class="address-button" @click="openYandexMaps">{{ address }}</button>
        </div>

        <a href="/" class="logo-link">
          <img :src="require('@/assets/img/user.png')" alt="user" class="user" />
        </a>

      </div>
    </div>
  </header>
</template>
  
<script>
export default {
  name: 'AddressButton',
  data() {
    return {
      address: 'Указать Адрес',
      search: '',
    }
  },
  methods: {
    openYandexMaps() {
      // Открыть Yandex.Maps с использованием адреса
      const url = `https://yandex.ru/maps/?text=${encodeURIComponent(this.address)}`
      window.open(url, '_blank')
    },
    logInput() {
      console.log('Input value:', this.search)
    },
    getAddress() {
    // Get user's address using Yandex Geolocation API
      navigator.geolocation.getCurrentPosition(
        async (position) => {
          const { latitude, longitude } = position.coords
          const response = await fetch(`https://geocode-maps.yandex.ru/1.x/?format=json&geocode=${longitude},${latitude}`)
          const data = await response.json()
          const address = data.response.GeoObjectCollection.featureMember[0].GeoObject.name
          this.address = address
        },
        (error) => {
          console.log('Error occurred. Please try again.', error)
        }
    )
  },
},
}
</script>

